html {
  height: 100%;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(4, 10, 5)), color-stop(85%, rgb(47, 105, 57)));
  background-image: -webkit-linear-gradient(rgb(4, 10, 5), rgb(47, 105, 57) 85%);
  background-image: -o-linear-gradient(rgb(4, 10, 5), rgb(47, 105, 57) 85%);
  background-image: linear-gradient(rgb(4, 10, 5), rgb(47, 105, 57) 85%);
}

body {
  margin: 0 auto;
  padding: 0;
  max-width: 1690px; 
  font-family: sans-serif;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(4, 10, 5)), color-stop(85%, rgb(47, 105, 57)));
  background-image: -webkit-linear-gradient(rgb(4, 10, 5), rgb(47, 105, 57) 85%);
  background-image: -o-linear-gradient(rgb(4, 10, 5), rgb(47, 105, 57) 85%);
  background-image: linear-gradient(rgb(4, 10, 5), rgb(47, 105, 57) 85%);
  width: 100%;
  min-height: 100%;
}



